
import { defineComponent } from 'vue';
import paginate from 'jw-paginate';

interface Page {
  id: number;
  title: string;
  thumbnail: string;
}

interface Pager {
  totalItems: number;
  currentPage: number;
  pageSize: number;
  totalPages: number;
  startPage: number;
  endPage: number;
  startIndex: number;
  endIndex: number;
  pages: number[];
}

export default defineComponent({
  name: 'Product',
  props: {
    paging: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      numOfColumn: 5,
      pager: {} as Pager,
      pageOfItems: [] as Page[],
    };
  },
  methods: {
    onPageChanged() {
      console.log('onPageChanged');
    },
    getProducts(curPage: number) {
      const numOfProducts = !this.$props.paging ? 10 : 160;
      console.log(numOfProducts);
      const items = [...Array(numOfProducts).keys()].map((i) => ({
        id: i + 1,
        title: 'Phôi cao su sẻ sấy ' + (i + 1),
        thumbnail: 'http://gobencat.bizz.vn/img_products/4650/b1.jpg',
      }));

      // get page from query params or default to first page
      const page = curPage || 1;

      // get pager object for specified page
      const pageSize = 12;
      const pager = paginate(items.length, page, pageSize);

      // get page of items from items array
      const pageOfItems = items.slice(pager.startIndex, pager.endIndex + 1);

      // return pager object and current page of items
      return { pager, pageOfItems };
    },
  },
  watch: {
    '$route.query.page': {
      immediate: true,
      handler(page: number) {
        page = page || 1;
        console.log('watch page: ', page, this.pager.currentPage);
        if (page !== this.pager.currentPage) {
          const { pager, pageOfItems } = this.getProducts(page);
          console.log('pager: ', pageOfItems);
          this.pager = pager;
          this.pageOfItems = pageOfItems;
        }
      },
    },
  },
});
